<template>
  <div class="blAdd">
      <div class="blAddMain">
          <div class="row">
              <div class="text">
                  博客标题 :
              </div>
              <div class="inpbox">
                  <input type="text" v-model="myData.title">
              </div>
          </div>
          <div class="row">
              <div class="text">
                  作者 :
              </div>
              <div class="inpbox" >
                  <input type="text" v-model="myData.author">
              </div>
          </div>
          <div class="row">
              <div class="text">
                  分类 :
              </div>
              <div class="inpbox">
                 <select v-model="myData.classify">
                     <option v-for="item in arList" :key="item.id">{{item.classname}}</option>
                 </select>
              </div>
          </div>
          <div class="row">
              <div class="text">
                  博客内容 :
              </div>
              <div class="inpbox">
                  <textarea v-model="myData.content"></textarea>
              </div>
          </div>
          <div class="row commit">
              <button @click="submit() ">发表博客</button>
          </div>
      </div>
      <div class="blshow" v-if="review">
          <Blitem :myObject="myData"></Blitem>
      </div>
  </div>
</template>

<script>
import Blitem from './Blitem'
// import axios from 'axios'
export default {
  data () {
    return {
      myData: {
        title: '',
        author: '',
        posttime: new Date().getTime() / 1000,
        content: '',
        classify: ''
      },
      arList: []
    }
  },
  components: {
    Blitem
  },
  computed: {
    review () {
      let isShow = false
      if (this.myData.title || this.myData.content || this.myData.author ||
            this.myData.classify) {
        isShow = true
      }
      return isShow
    }
  },
  methods: {
    submit () {
      for (var pro in this.myData) {
        if (!this.myData[pro]) {
          alert('请输入完整内容')
          return
        }
      }
      this.axios.get('https://ku.qingnian8.com/dataApi/blog/addBlog.php', {
        params: this.myData
      })
        .then(ret => {
          if (ret.data.code == 200) {
            alert('发步成功')
            this.$router.push('/show')
          }
        })
    }
  },
  mounted () {
    this.axios.get('https://ku.qingnian8.com/dataApi/blog/classBlog.php')
      .then(ret => {
        this.arList = ret.data
      })
  }
}
</script>

<style lang="less" scoped>
    .blAdd{
        padding: 50px 50px;
        .blAddMain{
            padding: 0 80px 0 0;
            .row{
                letter-spacing: 1px;
                display: flex;
                padding: 10px 0;
                align-items: center;
                .text{
                    width: 30%;
                    text-align: right;
                    padding: 0 20px 0 0;
                    font-size: 18px;
                }
                .inpbox{
                    width: 70%;
                    input,textarea,select{
                        width: 100%;
                        padding: 10px;
                        border: 1px solid #41b883;
                        outline:none;
                        border-radius: 5px;
                        transition: 0.3s;
                        &:focus{
                            box-shadow: 0 0 8px rgba(65,184,131);
                        }
                    }
                    textarea{
                        height: 150px;
                    }
                    select{
                        width: 40%;
                        option{
                            padding: 5px;
                            font-size: 14px;
                            height: 30px;
                        }
                    }
                }
            }
            .commit {
                margin-left: 30%;
            }
            .commit button{
                width: 50%;
                height:38px;
                background-color: #41b883;
                color: #fff;
                outline:none;
                border: none;
                border-radius: 5px;
                font-size: 16px;
                letter-spacing: 1px;
                &:hover{
                    background-color: rgba(65,184,131,0.7)

                }
            }
        }
    }
</style>
