
 <template>
    <div class="blshow">
       <Blitem :key="item.index" v-for="item in arList" :myObject="item" />
       <div class="but">
           <button @click="lastPage">&lt; 上一页</button>
           <button @click="nextPage">下一页   &gt;</button>
       </div>
    </div>

 </template>

<script>
import Blitem from './Blitem'
export default {
  data () {
    return {
      arList: [],
      myPage: 1,
      myNum: 6
    }
  },
  methods: {
    getdata (Num = this.myNum, Page = this.myPage) {
      this.axios.get('https://ku.qingnian8.com/dataApi/blog/showBlog.php?num=6', {
        params: {
          num: Num,
          page: Page
        }
      })
        .then(ret => {
          console.log(ret.data)
          this.arList = ret.data
        })
    },
    lastPage () {
      if (this.myPage > 1) {
        this.myPage--
        this.getdata()
      } else {
        alert('已经是最后一页了')
      }
    },
    nextPage () {
      if (this.arList.length < this.myNum || this.arList.length == 0) {
        alert('已经是最后一页了')
      } else {
        this.myPage++
        this.getdata()
      }
    }
  },
  components: {
    Blitem
  },
  mounted () {
    this.getdata()
  }
}
</script>
 <style lang="less" scoped>
     .blshow{
         padding: 50px 30px;
         .but{
             display: flex;
             justify-content: center;
             button{
                 outline: none;
                 color: #41B883;
                 background: #fff;
                 border: 1px solid #41b883;
                 padding: 6px 10px;
                 border-radius: 5px;
                 font-size: 16px;
                 margin-left: 6px;
                 cursor: pointer;
                 &:hover{
                     background: #41b883;
                     color: #fff;
                 }
             }
         }
     }
 </style>
