import Vue from 'vue'
import VueRouter from 'vue-router'

import Bladd from '../components/Bladd.vue'
import Blshow from '../components/Blshow'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/show'
  },
  {
    path: '/show',
    component: Blshow
  },
  {
    path: '/add',
    component: Bladd
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: 'myClass'
})

export default router
