<template>
   <div class="blitem">
        <div class="top">
            <h2>{{mytitle(myObject.title,8)}}</h2>
            <div class="time">{{mytime(myObject.posttime,1)}}</div>
        </div>
        <div class="content">{{myObject.content}}</div>
        <div class="footer">
            <div class="list">
                所属分类：
                <span>{{myObject.classify}}</span>
            </div>
        <div class="author">作者：<span>{{myObject.author}}</span></div>
        </div>
    </div>

</template>

<script>
import comment from '../assets/comment.js'
export default {
  data () {
    return {}
  },
  methods: {
    mytitle (str, num) {
      return comment.getStrLen(str, num)
    },
    mytime (time, type) {
      return comment.myDate(time, type)
    }
  },
  props: {
    myObject: {
      type: Object,
      default () {
        return {
          title: '123',
          time: 456
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
    .blitem{
        padding: 30px 0;
        .top{
            display: flex;
            justify-content: space-between;
            h2{
                font-size: 18px;
                color: #333;
            }
            .time{
                font-size: 14px;
                color: #666;
            }

        }
        .content{
            padding: 10px;
            margin: 10px 0 15px;
            background: #f6f6f6;
            border-radius: 5px;
            line-height: 1.8em;
            color:#333;
            font-size: 16px;
        }
        .footer{
            display: flex;
            justify-content: space-between;
            align-items: center;
            color: #888;
            font-size: 12px;
            span{
                color: #444;
                font-size: 14px;
            }
    }
}
</style>
